import PropTypes from 'prop-types';
import {useCallback, useEffect, useRef, useState} from "react";
import {WaveForm, WaveSurfer} from "wavesurfer-react";
import {Button, Card, IconButton, Slider, Stack, Typography} from "@mui/material";
import {PauseCircleFilled, PlayCircleFilled, Send} from '@material-ui/icons';
import {Box} from "@material-ui/core";
import {doc, onSnapshot} from "firebase/firestore";
import {db} from "../firestore";
import {marks2, marks2Short, shuffleArray} from "../utils/utils";
import {useSelector} from "react-redux";
import {accountStep} from "../store/selectors/account.selector";
import {useWindowSize} from "@react-hook/window-size";

export default function AudioSimilarity(props) {
  const { onUpdate } = props
  const waveSurfer = useRef()
  const waveSurfer2 = useRef()
  const similarityFiles = useRef([])
  const similarityFiles2 = useRef([])
  const similarityPercent = useRef([])
  const similarityPercent2 = useRef([])
  const similarityValue = useRef([])
  const samplePrev = useRef('')
  const samplePrev2 = useRef('')
  const controlPrev = useRef(3)
  const enabledPrev = useRef(false)
  const playingPrev = useRef(false)
  const playingPrev2 = useRef(false)
  const totalPrev = useRef(0)
  const sampleNext = useRef(1)
  const progress = useRef(0)
  const progress2 = useRef(0)
  const percent = useRef(50)

  const currentStep = useSelector(accountStep);

  const [ready, setReady] = useState(false)
  const [ready2, setReady2] = useState(false)
  const [playing, setPlaying] = useState(false)
  const [playing2, setPlaying2] = useState(false)
  const [enabled, setEnabled] = useState(enabledPrev.current) // enabled next button
  const [control, setControl] = useState(controlPrev.current) // required number of mixins
  const [sampleN, setSampleN] = useState(1) // current sample number
  const [samples, setSamples] = useState([]) // list of file names
  const [samples2, setSamples2] = useState([]) // list of file names
  const [rating, setRating] = useState(4) // value of slider
  const [total, setTotal] = useState(totalPrev.current) // required number of samples in test
  const [width] = useWindowSize();

  useEffect(() => {
    const unsubscribe = onSnapshot(doc(db, "settings", "current"), snapshot => {
      if (snapshot.exists()) {
        const { similarityControl, similarityN, percentToListen } = snapshot.data()
        // console.log({ similarityControl, similarityN, percentToListen })
        setTotal(similarityN)
        setControl(similarityControl)
        percent.current = percentToListen
        // console.log(percent.current)
      } else {
        console.log('emp')
      }
    })
    return () => {
      unsubscribe()
    }
  }, [props.firebase])

  useEffect(() => {
    // console.log(control, sampleN, total, samples.length)
    if (currentStep === 7 && sampleN === 1 && (total !== totalPrev.current || control !== controlPrev.current)) {
      console.log('Generate set of pairs')
      totalPrev.current = total
      controlPrev.current = control
      const arr = []
      const arr2 = []
      for (let f = 0; f < 5; f++) {
        for (let i = 0; i < total / 5; i++) {
          const s = Math.ceil(Math.random() * 10)
          const u = Math.ceil(Math.random() * 10)
          const name = `/${f}/spk_${s}/${f}_spk_${s}_utt_${u}.wav`
          if (arr.includes(name)) {
            i--
            continue
          }
          arr.push(name)
        }
      }
      shuffleArray(arr)
      for (let i = 0; i < total; i++) {
        const speaker = arr[i].slice(1, 2)
        if (speaker === '0') {
          const split = arr[i].split('_utt_')
          if (split.length > 1) {
            const utt = split[1].replace(/[^0-9]/g, '')
            split[1] = ((utt - 1 + (Math.ceil(Math.random() * 9))) % 10 + 1) + '.wav'
          }
          arr2[i] = split.join('_utt_')
        } else {
          if (Math.random() > 0.5) {
            arr2[i] = arr[i].replace('/' + speaker, '/0').replace('/' + speaker, '/0')
          } else {
            arr2[i] = arr[i]
            arr[i] = arr2[i].replace('/' + speaker, '/0').replace('/' + speaker, '/0')
          }
        }
        // console.log(arr[i], arr2[i])
      }
      if (control <= total - 5 && control <= 5) {
        const arrTemp = []
        for (let i = 0; i < control; i++) {
          const r = Math.ceil(Math.random() * 5)
          const name = `/similarity_control/pair_${r}/similarity_control_${r}_a.wav`
          const name2 = `/similarity_control/pair_${r}/similarity_control_${r}_b.wav`
          if (arrTemp.includes(name)) {
            i--
            continue
          }
          const f = Math.ceil(Math.random() * (total - 5)) + 4
          if (arr[f].includes('similarity_control')) {
            i--
            continue
          }
          arr.push(arr[f])
          arr2.push(arr2[f])
          arrTemp.push(name)
          arr[f] = name
          arr2[f] = name2
        }
      }
      // console.log(arr)
      setSamples(arr)
      setSamples2(arr2)
      samplePrev.current = `/survey_data${arr[0]}`
      waveSurfer.current.load(`/survey_data${arr[0]}`)
      samplePrev2.current = `/survey_data${arr2[0]}`
      waveSurfer2.current.load(`/survey_data${arr2[0]}`)
      // console.log(arr)
      // be sure, that waveSurfer is redrawn properly
      setTimeout(() => waveSurfer.current.drawBuffer(), 100)
      setTimeout(() => waveSurfer.current.drawBuffer(), 200)
      setTimeout(() => waveSurfer.current.drawBuffer(), 1400)
      setTimeout(() => waveSurfer.current.drawBuffer(), 2000)
    }
  }, [sampleN, total, control, samples.length, currentStep])

  const handleWSMount = useCallback((surfer) => {
    // console.log('init2')
    waveSurfer.current = surfer
    waveSurfer.current.toggleInteraction()
    waveSurfer.current.on('finish', function () {
      progress.current = 100
      setPlaying(false)
      playingPrev.current = false
    })
    waveSurfer.current.on('audioprocess', function (current) {
      progress.current = current / waveSurfer.current.getDuration() * 100
      if (!enabledPrev.current) {
        if (progress.current > percent.current && progress2.current > percent.current) {
          enabledPrev.current = true
          setEnabled(true)
        }
      }
    })
    waveSurfer.current.on('ready', function () {
      setReady(true)
      if (playingPrev.current) {
        // run if play was pressed before
        waveSurfer.current.playPause();
      }
      // auto play for next step
      // console.log('load', [samplePrev.current, samplePrev2.current])
      if (sampleNext.current > 1) {
        // setPlaying(true)
        // waveSurfer.current.playPause();
      }
    })
  }, [])

  const handleWSMount2 = useCallback((surfer) => {
    // console.log('init3')
    waveSurfer2.current = surfer
    waveSurfer2.current.toggleInteraction()
    waveSurfer2.current.on('finish', function () {
      progress2.current = 100
      setPlaying2(false)
      playingPrev2.current = false
    })
    waveSurfer2.current.on('audioprocess', function (current) {
      progress2.current = current / waveSurfer2.current.getDuration() * 100
      if (!enabledPrev.current) {
        if (progress.current > percent.current && progress2.current > percent.current) {
          enabledPrev.current = true
          setEnabled(true)
        }
      }
    })
    waveSurfer2.current.on('ready', function () {
      setReady2(true)
      if (playingPrev2.current) {
        // run if play was pressed before
        waveSurfer2.current.playPause();
      }
    })
  }, [])

  const play = useCallback(() => {
    playingPrev.current = !playingPrev.current
    setPlaying(playingPrev.current)
    if (ready) {
      waveSurfer.current.playPause();
      waveSurfer.current.drawBuffer();
      if (waveSurfer2.current.isPlaying()) {
        setPlaying2(false)
        playingPrev2.current = false
        waveSurfer2.current.playPause()
      }
    }
  }, [ready])

  const play2 = useCallback(() => {
    playingPrev2.current = !playingPrev2.current
    setPlaying2(playingPrev2.current)
    if (ready2) {
      waveSurfer2.current.playPause();
      waveSurfer2.current.drawBuffer();
      if (waveSurfer.current.isPlaying()) {
        setPlaying(false)
        playingPrev.current = false
        waveSurfer.current.playPause()
      }
    }
  }, [ready2])

  const onNext = useCallback(() => {
    // similarityFiles.current[sampleNext.current - 1] = [samples[sampleNext.current - 1], rating, Math.round(progress.current)]
    similarityFiles.current[sampleNext.current - 1] = samples[sampleNext.current - 1]
    similarityFiles2.current[sampleNext.current - 1] = samples2[sampleNext.current - 1]
    similarityPercent.current[sampleNext.current - 1] = Math.round(progress.current)
    similarityPercent2.current[sampleNext.current - 1] = Math.round(progress2.current)
    similarityValue.current[sampleNext.current - 1] = rating
    // console.log(progress.current)
    sampleNext.current++
    setSampleN(sampleNext.current)
    progress.current = 0
    progress2.current = 0
    setPlaying(false)
    setPlaying2(false)
    playingPrev.current = false
    playingPrev2.current = false
    enabledPrev.current = false
    setEnabled(false)
    setRating(4)
    if (sampleNext.current === samples.length + 1) {
      if (waveSurfer.current.isPlaying()) {
        waveSurfer.current.playPause();
      }
      if (waveSurfer2.current.isPlaying()) {
        waveSurfer2.current.playPause();
      }
      onUpdate({ similarityFiles: similarityFiles.current, similarityFiles2: similarityFiles2.current, similarityPercent: similarityPercent.current, similarityPercent2: similarityPercent2.current, similarityValue: similarityValue.current })
    } else {
      setReady(false)
      setReady2(false)
      samplePrev.current = `/survey_data${samples[sampleNext.current - 1]}`
      waveSurfer.current.load(`/survey_data${samples[sampleNext.current - 1]}`)
      samplePrev2.current = `/survey_data${samples2[sampleNext.current - 1]}`
      waveSurfer2.current.load(`/survey_data${samples2[sampleNext.current - 1]}`)
    }
  }, [onUpdate, samples, samples2, rating])

  return (
    <Stack direction="column" sx={{ mt: 8 }}>
      <Card sx={{ p: 3, background: 'linear-gradient(to right, rgba(2, 23, 92, 0.9), rgba(5, 11, 36, 0.9))', borderRadius: 8 }}>
        <Typography variant="h5" sx={{ mb: 1, color: 'white', textAlign: 'center' }}>
          Similarity test {sampleN} of {samples.length}
        </Typography>

        <Stack justifyContent="center" direction="row">
          <IconButton aria-label="play/pause" color="purple" sx={{ width: '120px', height: '120px', ml: '-20px' }} onClick={play}>
            { playing ? <PauseCircleFilled style={{ width: '70px', height: '70px' }} /> : <PlayCircleFilled style={{ width: '70px', height: '70px' }} />}
          </IconButton>

          <Box sx={{ width: '500px', height: '120px', ml: '-10px', mt: '1px' }}>
            <WaveSurfer onMount={handleWSMount}>
              <WaveForm
                id="waveform2"
                interact="true"
                fillParent="false"
                barWidth="3"
                cursorWidth="1"
                backend="WebAudio"
                width="450"
                height="120"
                progressColor="#2D5BFF"
                removeMediaElementOnDestroy="false"
                responsive="true"
                waveColor="#EFEFEF"
                cursorColor="transparent"
                minPxPerSec="10"
              />
            </WaveSurfer>
          </Box>
        </Stack>

        <Stack justifyContent="center" direction="row">
          <IconButton aria-label="play/pause" color="purple" sx={{ width: '120px', height: '120px', ml: '-20px' }} onClick={play2}>
            { playing2 ? <PauseCircleFilled style={{ width: '70px', height: '70px' }} /> : <PlayCircleFilled style={{ width: '70px', height: '70px' }} />}
          </IconButton>

          <Box sx={{ width: '500px', height: '120px', ml: '-10px', mt: '1px' }}>
            <WaveSurfer onMount={handleWSMount2}>
              <WaveForm
                id="waveform3"
                interact="true"
                fillParent="false"
                barWidth="3"
                cursorWidth="1"
                backend="WebAudio"
                width="450"
                height="120"
                progressColor="#2D5BFF"
                removeMediaElementOnDestroy="false"
                responsive="true"
                waveColor="#EFEFEF"
                cursorColor="transparent"
                minPxPerSec="10"
              />
            </WaveSurfer>
          </Box>
        </Stack>
        <Box sx={{ mx: 5, mt: 1 }}>
          <Slider
            color="secondary"
            aria-label="similarity"
            defaultValue={4}
            step={0.5}
            min={1}
            max={5}
            valueLabelDisplay="auto"
            marks={width > 550 ? marks2 : marks2Short}
            value={rating}
            onChange={(event, value) => setRating(value)}
          />
        </Box>
      </Card>
      <Button variant="contained" size="large" color="purple" endIcon={<Send/>} onClick={onNext} disabled={!enabled}
              sx={{mt: 4, width: 300, alignSelf: 'center'}}>
        {sampleN === samples.length ? 'Submit and finish' : 'Submit and go to next'}
      </Button>
    </Stack>
  );
}

AudioSimilarity.propTypes = {
  onUpdate: PropTypes.func,
};
