import React, {useCallback, useEffect, useRef, useState} from 'react';
import './App.css';
import Back from "./components/Back";
import Div100vh from 'react-div-100vh'
import {Box, CircularProgress, IconButton, Stack, Typography} from "@mui/material";
import SwipeableViews from "react-swipeable-views";
import TabPanelWizard from "./components/TabPanelWizard";
import AudioNatural from "./components/AudioNatural";
import InputId from "./components/InputId";
import {ThemeProvider} from "@emotion/react";
import theme from "./theme";
import {v4 as uuid} from 'uuid';
import Instructions from "./components/Instructions";
import {useDispatch, useSelector} from "react-redux";
import {accountSet, saveSurvey, sendTelegram} from "./store/actions/account.actions";
import {accountBack, accountStep} from "./store/selectors/account.selector";
import InputDevice from "./components/InputDevice";
import {analyticsEvent} from "./firestore";
import Instructions2 from "./components/Instructions2";
import store from "./store/store";
import Instructions3 from "./components/Instructions3";
import ThankYou from "./components/ThankYou";
import AudioSimilarity from "./components/AudioSimilarity";
import marbleImage from './assets/marble-small.png'
import marbleDisabled from './assets/marble-disabled.png'
import {browserVersion, isSafari} from "react-device-detect";
import {copyToClipboard} from "./utils/utils";
import {Helmet} from "react-helmet";

console.log(process.env.REACT_APP_VERSION)

function App() {
  const backRef = useRef();
  const dispatch = useDispatch();

  const currentStep = useSelector(accountStep);
  const currentBack = useSelector(accountBack);

  const [activeStep, setActiveStep] = useState(0);

  const handleUserKeyPress = useCallback((event) => {
    const {code, altKey} = event;
    if (code === 'KeyR' && altKey) {
      setActiveStep(1)
      dispatch(accountSet({step: 1}))
    }
    if (code === 'KeyB' && altKey) {
      dispatch(accountSet({back: !currentBack}))
      backRef.current.changeBack(!currentBack)
    }
  }, [dispatch, currentBack])

  // state effect
  useEffect(() => {
    let unmount = false
    setTimeout(() => {
      if (!unmount) {
        setActiveStep(currentStep)
      }
    }, 1300)
    // register key listener
    window.addEventListener('keydown', handleUserKeyPress);
    return () => {
      unmount = true
      window.removeEventListener('keydown', handleUserKeyPress);
    };
  }, [handleUserKeyPress, currentStep]);

  const switchBack = useCallback((phase) => {
    backRef.current.changeBack(!currentBack)
    dispatch(accountSet({back: !currentBack}))
  }, [currentBack, dispatch]);

  const onBack = useCallback((phase) => {
    backRef.current?.changePhase(phase)
  }, []);

  const onTap = useCallback((phase) => {
    backRef.current?.setTap(phase)
  }, []);

  const onMouseDown = useCallback((value) => {
    onTap(true)
  }, [onTap])

  const onMouseUp = useCallback((value) => {
    onTap(false)
  }, [onTap])

  const onContextMenu = useCallback((event) => {
    event.preventDefault()
    event.stopPropagation()
    copyToClipboard(browserVersion)
    return false
  }, [])

  const update = useCallback((data) => {
    setActiveStep(step => {
      if (data) {
        const keys = Object.keys(data)
        if (keys.length) {
          const key0 = keys[0]
          if (key0 === 'naturalness' || key0 === 'similarity') {
            analyticsEvent('set_' + key0, {[key0]: data[key0][0]});
          } else {
            analyticsEvent('set_' + key0, {[key0]: data[key0]});
          }
        }
      }
      if (step === 5) {
        console.log(data)
        dispatch(accountSet({step: step + 1, ...data}))
        return step + 1
      }
      if (step === 7) {
        const completionCode = uuid()
        dispatch(accountSet({step: step + 1, ...data, completionCode}))
        store.dispatch(saveSurvey())
        store.dispatch(sendTelegram())
        return step + 1
      }
      dispatch(accountSet({step: step + 1, ...data}))
      // store.dispatch(saveSurvey(data))
      return step + 1
    })
  }, [dispatch])

  // console.log(isChrome, browserVersion)

  if (isSafari && +browserVersion<14) {
    return (
      <ThemeProvider theme={theme}>
        <Div100vh className="App">
          <Back ref={backRef} noMarble />
          <Box component={Stack} direction="column" justifyContent="center" sx={{ position: 'absolute', background: 'transparent', width: '100%', height: '100%', userSelect: 'none' }}>
            <Typography variant="h5" sx={{ color: 'white', textAlign: 'center' }}>
              Please, update your Safari to see this site!
            </Typography>
          </Box>
        </Div100vh>
      </ThemeProvider>
    )
  }

  return (
    <ThemeProvider theme={theme}>
      <Helmet>
        <title>AI MOS v.{process.env.REACT_APP_VERSION}</title>
      </Helmet>
      <Div100vh className="App">
        <Back ref={backRef} />
        <Box component={Stack} direction="column" justifyContent="center" sx={{ position: 'absolute', background: 'transparent', width: '100%', height: '100%', userSelect: 'none' }}>
          <Box sx={{ width: '100%', maxWidth: '600px', margin: '0 auto' }}>
            <SwipeableViews index={activeStep} disabled>
              <TabPanelWizard value={activeStep} index={0}>
                <Box width="100px" height="100px" margin="0 auto" padding="50px 0 0 0">
                  <CircularProgress />
                </Box>
              </TabPanelWizard>
              <TabPanelWizard value={activeStep} index={1}>
                <InputId onUpdate={update} onTap={onTap} />
              </TabPanelWizard>
              <TabPanelWizard value={activeStep} index={2}>
                <Instructions onUpdate={update} onTap={onTap} />
              </TabPanelWizard>
              <TabPanelWizard value={activeStep} index={3}>
                <InputDevice onUpdate={update} onTap={onTap} />
              </TabPanelWizard>
              <TabPanelWizard value={activeStep} index={4}>
                <Instructions2 onUpdate={update} onTap={onTap} />
              </TabPanelWizard>
              <TabPanelWizard value={activeStep} index={5}>
                <AudioNatural onUpdate={update} onTap={onTap} />
              </TabPanelWizard>
              <TabPanelWizard value={activeStep} index={6}>
                <Instructions3 onUpdate={update} onTap={onTap} />
              </TabPanelWizard>
              <TabPanelWizard value={activeStep} index={7}>
                {activeStep > 5 && (<AudioSimilarity onUpdate={update} onTap={onTap}/>)}
              </TabPanelWizard>
              <TabPanelWizard value={activeStep} index={8}>
                <ThankYou onUpdate={update} onBack={onBack} onTap={onTap} />
              </TabPanelWizard>
            </SwipeableViews>
          </Box>
        </Box>
        <IconButton aria-label="switch back" color="blue" sx={{ position: 'absolute', right: '10px', top: '10px' }} title={currentBack ? 'Turn off cool 3d animation' : 'Turn on cool 3d animation'}
                    onContextMenu={onContextMenu}
                    onClick={switchBack} onMouseDown={onMouseDown} onMouseUp={onMouseUp} onMouseLeave={onMouseUp} onTouchStart={onMouseDown} onTouchEnd={onMouseUp}>
          <img draggable="false" width="40" alt="switch back" src={currentBack ? marbleImage : marbleDisabled} />
        </IconButton>
      </Div100vh>
    </ThemeProvider>
  );
}

export default App;
