import PropTypes from 'prop-types';
import {useCallback, useState} from "react";
import {Button, Card, FormControlLabel, Radio, RadioGroup, Stack, Typography} from "@mui/material";
import {Send} from '@material-ui/icons';
import {useSelector} from "react-redux";
import {accountDevice} from "../store/selectors/account.selector";

export default function InputDevice(props) {
  const { onUpdate, onTap } = props;

  const deviceCurrent = useSelector(accountDevice)
  const [device, setDevice] = useState(deviceCurrent);

  const onMouseDown = useCallback((value) => {
    onTap(true)
  }, [onTap])

  const onMouseUp = useCallback((value) => {
    onTap(false)
  }, [onTap])

  return (
    <Stack direction="column" sx={{ mt: 8 }}>
      <Card sx={{ p: 3, background: 'linear-gradient(to right, rgba(2, 23, 92, 0.9), rgba(5, 11, 36, 0.9))', borderRadius: 8 }}>
        <Typography variant="h5" sx={{ mb: 2, color: 'white', textAlign: 'left' }}>
          How are you listening to speech samples?
        </Typography>
        <RadioGroup
          aria-label="gender"
          name="controlled-radio-buttons-group"
          value={device}
          onChange={(event) => setDevice(event.target.value)}
        >
          <FormControlLabel value="Headphones, with no noise" sx={{ color: 'white' }} color="purple" control={<Radio color="purple" />} label="Headphones, with no noise in the background" />
          <FormControlLabel value="Headphones, with low-level noise" sx={{ color: 'white' }} color="purple" control={<Radio color="purple" />} label="Headphones, with some low-level noise in the background" />
          <FormControlLabel value="Audio speakers or other" sx={{ color: 'white' }} color="purple" control={<Radio color="purple" />} label="Audio speakers or other" />
        </RadioGroup>
      </Card>
      <Button variant="contained" size="large" color="purple" endIcon={<Send/>} onClick={() => onUpdate({device})} disabled={device===''}
              sx={{mt: 4, width: 200, alignSelf: 'center'}} onMouseDown={onMouseDown} onMouseUp={onMouseUp} onMouseLeave={onMouseUp} onTouchStart={onMouseDown} onTouchEnd={onMouseUp}>
        Next
      </Button>
    </Stack>
  );
}

InputDevice.propTypes = {
  onUpdate: PropTypes.func,
  onTap: PropTypes.func,
};
