import PropTypes from 'prop-types';
import {Button, Card, Checkbox, FormControlLabel, Stack, Typography} from "@mui/material";
import {Send} from '@material-ui/icons';
import {useCallback, useState} from "react";

export default function Instructions3(props) {
  const {onUpdate, onTap} = props;
  const [rules, setRules] = useState(false)

  const onMouseDown = useCallback((value) => {
    onTap(true)
  }, [onTap])

  const onMouseUp = useCallback((value) => {
    onTap(false)
  }, [onTap])

  return (
    <Stack direction="column" sx={{mt: 12}}>
      <Card sx={{
        p: 3,
        background: 'linear-gradient(to right, rgba(2, 23, 92, 0.9), rgba(5, 11, 36, 0.9))',
        borderRadius: 8
      }}>
        <Typography variant="h4" sx={{color: 'white'}}>
          Test 2 of 2. Speaker Similarity.
        </Typography>
        <Typography variant="subtitle1" sx={{color: 'white', textAlign: 'left', mt: 3}}>
          Please listen two speech samples below (Sample A and Sample B) and rate how similar are the voices on
          the recordings. Your rating should reflect your evaluation of how close the voices of the two speakers sound
          to each other. You should not judge <b>the content, grammar and audio quality</b> of the sentences; instead, just
          focus on the similarity of the speakers to each other. We highly recommend to hear audio with <b>headphones</b> in
          the environment with <b>no background noise</b>. You should listen to at least 50% of each sample to proceed to the
          next step. <b>Note that the test contains both real and synthetic voices.</b>
        </Typography>
      </Card>
      <FormControlLabel
        sx={{ margin: '10px auto', color: '#fff' }}
        label="I have read and understood instructions"
        control={(
          <Checkbox
            sx={{ ml: -2 }}
            color="purple"
            id="outlined-password-update"
            checked={rules}
            onChange={() => setRules(!rules)}
          />
        )}
      />
      <Button variant="contained" size="large" color="purple" endIcon={<Send/>} onClick={() => onUpdate()} disabled={!rules}
              sx={{mt: 0, width: 200, alignSelf: 'center'}} onMouseDown={onMouseDown} onMouseUp={onMouseUp} onMouseLeave={onMouseUp} onTouchStart={onMouseDown} onTouchEnd={onMouseUp}>
        Start
      </Button>
    </Stack>
  );
}

Instructions3.propTypes = {
  onUpdate: PropTypes.func,
  onTap: PropTypes.func,
};
