import PropTypes from 'prop-types';
import {Button, Card, Stack, Typography} from "@mui/material";
import {Send} from '@material-ui/icons';
import {useCallback} from "react";

export default function Instructions(props) {
  const { onUpdate, onTap } = props;

  const onMouseDown = useCallback((value) => {
    onTap(true)
  }, [onTap])

  const onMouseUp = useCallback((value) => {
    onTap(false)
  }, [onTap])

  return (
    <Stack direction="column" sx={{ mt: 8 }}>
      <Card sx={{ p: 3, background: 'linear-gradient(to right, rgba(2, 23, 92, 0.9), rgba(5, 11, 36, 0.9))', borderRadius: 8 }}>
        <Typography variant="h4" sx={{ color: 'white' }}>
          Instructions.
        </Typography>
        <Typography variant="subtitle1" sx={{ color: 'white', textAlign: 'left', mt: 3 }}>
          In this task, your job is to evaluate the naturalness of speech. Please start working on this task only if all below is true:
          <ul>
            <li>You have good listening ability.</li>
            <li>There is no considerable background noise (street noise, people talking, open TV/radio, etc.).</li>
            <li>You can hear all audio samples.</li>
          </ul>
        </Typography>
      </Card>
      <Button variant="contained" size="large" color="purple" endIcon={<Send />} onClick={() => onUpdate()}
              sx={{ mt: 4, width: 200, alignSelf: 'center' }} onMouseDown={onMouseDown} onMouseUp={onMouseUp} onMouseLeave={onMouseUp} onTouchStart={onMouseDown} onTouchEnd={onMouseUp}>
        Confirm
      </Button>
    </Stack>
  );
}

Instructions.propTypes = {
  onUpdate: PropTypes.func,
  onTap: PropTypes.func,
};
