import PropTypes from 'prop-types';
import {Button, Card, Stack, TextField, Typography} from "@mui/material";
import {useSelector} from "react-redux";
import {accountCompletionCode} from "../store/selectors/account.selector";
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import {useCallback, useRef, useState} from "react";
import {copyToClipboard} from "../utils/utils";

export default function ThankYou(props) {
  const { onBack, onTap } = props;

  const code = useSelector(accountCompletionCode);
  const codeRef = useRef()
  const [green, setGreen] = useState(false)

  const onMouseDown = useCallback((value) => {
    onTap(true)
  }, [onTap])

  const onMouseUp = useCallback((value) => {
    onTap(false)
  }, [onTap])

  const clickFinish = useCallback(() => {
    const input = codeRef.current.querySelector('input')
    if (input) {
      input.focus()
      copyToClipboard(code)
    }
    setGreen(true)
    onBack(3)
  }, [code, onBack])

  return (
    <Stack direction="column" sx={{ mt: 8 }}>
      <Card sx={{ p: 3, background: green ? 'linear-gradient(to right, rgba(2, 92, 23, 0.9), rgba(5, 36, 11, 0.9))' : 'linear-gradient(to right, rgba(2, 23, 92, 0.9), rgba(5, 11, 36, 0.9))', borderRadius: 8 }}>
        <Typography variant="h4" sx={{ color: 'white' }}>
          Well done!
        </Typography>
        <Typography variant="subtitle1" sx={{ color: 'white', mt: 3 }}>
          Thank you for taking the test. Enter the following code as completion code in your MTurk account.
        </Typography>
        <TextField ref={codeRef} label="" variant="filled" value={code || ''} sx={{ width: '100%', mt: 2 }}
                   InputProps={{ readOnly: true, style: {background: green ? 'linear-gradient(to left, rgba(2, 92, 23, 0.9), rgba(5, 36, 11, 0.9))' : 'linear-gradient(to left, rgba(2, 23, 92, 0.9), rgba(5, 11, 36, 0.9))'}}}
                   inputProps={{style: {fontSize: 26, color: 'yellow', textAlign: 'center' }}}/>
      </Card>
      <Button variant="contained" size="large" color={green ? 'darkgreen' : 'purple'} endIcon={<ContentCopyIcon/>}
              onClick={clickFinish} onMouseDown={onMouseDown} onMouseUp={onMouseUp} onMouseLeave={onMouseUp} onTouchStart={onMouseDown} onTouchEnd={onMouseUp}
              sx={{mt: 4, width: 300, alignSelf: 'center'}}>
        {green ? 'Code copied to clipboard' : 'Copy code to clipboard'}
      </Button>
    </Stack>
  );
}

ThankYou.propTypes = {
  onBack: PropTypes.func,
  onTap: PropTypes.func,
};
