const emptyState = {
  back: false,
  step: 1,
  turkId: '',
  device: '',
  naturalnessFiles: '',
  naturalnessValue: '',
  naturalnessPercent: '',
  completionCode: '',
};

function getAccount() {
  try {
    return localStorage.account ? JSON.parse(localStorage.account) ?? emptyState : emptyState;
  } catch (error) {
    return emptyState;
  }
}

const account = getAccount()

// console.log(111, JSON.parse(localStorage.account))

const initialState = {
  back: account.back ?? true,
  step: account.step ?? 1,
  turkId: account.turkId,
  device: account.device,
  naturalnessFiles: account.naturalnessFiles || [],
  naturalnessValue: account.naturalnessValue || [],
  naturalnessPercent: account.naturalnessPercent || [],
  completionCode: account.completionCode,
};

function accountReducer(state = initialState, action) {
  // console.log(action, state);
  switch (action.type) {
    case 'account/set': {
      localStorage.account = JSON.stringify({...state, ...action.payload});
      return { ...state,...action.payload };
    }
    default:
      return state;
  }
}

export default accountReducer;
