import PropTypes from 'prop-types';
import {useCallback, useEffect, useRef, useState} from "react";
import {WaveForm, WaveSurfer} from "wavesurfer-react";
import {Button, Card, IconButton, Slider, Stack, Typography} from "@mui/material";
import {PauseCircleFilled, PlayCircleFilled, Send} from '@material-ui/icons';
import {Box} from "@material-ui/core";
import {doc, onSnapshot} from "firebase/firestore";
import {db} from "../firestore";
import {marks, marksShort, shuffleArray} from "../utils/utils";
import {useSelector} from "react-redux";
import {accountStep} from "../store/selectors/account.selector";
import {useWindowSize} from "@react-hook/window-size";

export default function AudioNatural(props) {
  const { onUpdate } = props;
  const waveSurfer = useRef()
  const naturalnessFiles = useRef([])
  const naturalnessPercent = useRef([])
  const naturalnessValue = useRef([])
  const samplePrev = useRef('')
  const controlPrev = useRef(3)
  const enabledPrev = useRef(false)
  const playingPrev = useRef(false)
  const totalPrev = useRef(0)
  const sampleNext = useRef(1)
  const progress = useRef(0)
  const percent = useRef(50)

  const currentStep = useSelector(accountStep);

  const [ready, setReady] = useState(false)
  const [playing, setPlaying] = useState(false)
  const [enabled, setEnabled] = useState(enabledPrev.current) // enabled next button
  const [control, setControl] = useState(controlPrev.current) // required number of mixins
  const [sampleN, setSampleN] = useState(1) // current sample number
  const [samples, setSamples] = useState([]) // list of file names
  const [rating, setRating] = useState(4) // value of slider
  const [total, setTotal] = useState(totalPrev.current) // required number of samples in test
  const [width] = useWindowSize();

  useEffect(() => {
    const unsubscribe = onSnapshot(doc(db, "settings", "current"), snapshot => {
      if (snapshot.exists()) {
        const { naturalnessControl, naturalnessN, percentToListen } = snapshot.data()
        // console.log({ naturalnessControl, naturalnessN, percentToListen })
        setTotal(naturalnessN)
        setControl(naturalnessControl)
        percent.current = percentToListen
        // console.log(percent.current)
      } else {
        console.log('emp')
      }
    })
    return () => {
      unsubscribe()
    }
  }, [props.firebase])

  useEffect(() => {
    // console.log(control, sampleN, total, samples.length)
    // console.log(currentStep, sampleN, total, totalPrev.current, controlPrev.current)
    if (currentStep === 5 && sampleN === 1 && (total !== totalPrev.current || control !== controlPrev.current)) {
      console.log('Generate set of samples')
      totalPrev.current = total
      controlPrev.current = control
      const arr = []
      for (let f = 0; f < 5; f++) {
        for (let i = 0; i < total / 5; i++) {
          const s = Math.ceil(Math.random() * 10)
          const u = Math.ceil(Math.random() * 10)
          const name = `/${f}/spk_${s}/${f}_spk_${s}_utt_${u}.wav`
          if (arr.includes(name)) {
            i--
            continue
          }
          arr.push(name)
        }
      }
      shuffleArray(arr)
      if (control <= total - 5 && control <= 5) {
        const arrTemp = []
        for (let i = 0; i < control; i++) {
          const r = Math.ceil(Math.random() * 5)
          const name = `/naturalness_control/naturalness_control_${r}.wav`
          if (arrTemp.includes(name)) {
            i--
            continue
          }
          const f = Math.ceil(Math.random() * (total - 5)) + 4
          if (arr[f].includes('naturalness_control')) {
            i--
            continue
          }
          arr.push(arr[f])
          arrTemp.push(name)
          arr[f] = name
        }
      }
      // console.log(arr)
      setSamples(arr)
      samplePrev.current = `/survey_data${arr[0]}`
      waveSurfer.current.load(`/survey_data${arr[0]}`)
      // console.log(arr)
      // be sure, that waveSurfer is redrawn properly
      setTimeout(() => waveSurfer.current.drawBuffer(), 100)
      setTimeout(() => waveSurfer.current.drawBuffer(), 200)
      setTimeout(() => waveSurfer.current.drawBuffer(), 1400)
      setTimeout(() => waveSurfer.current.drawBuffer(), 2000)
    }
  }, [sampleN, total, control, samples.length, currentStep])

  const handleWSMount = useCallback((surfer) => {
    console.log('init')
    waveSurfer.current = surfer
    waveSurfer.current.toggleInteraction()
    waveSurfer.current.on('finish', function () {
      progress.current = 100
      setPlaying(false)
      playingPrev.current = false
    })
    waveSurfer.current.on('audioprocess', function (current) {
      progress.current = current / waveSurfer.current.getDuration() * 100
      if (!enabledPrev.current) {
        if (progress.current > percent.current) {
          enabledPrev.current = true
          setEnabled(true)
        }
      }
    });
    waveSurfer.current.on('ready', function () {
      setReady(true)
      if (playingPrev.current) {
        // run if play was pressed before
        waveSurfer.current.playPause();
      }
      // auto play for next step
      // console.log('load', [samplePrev.current])
      if (sampleNext.current > 1) {
        // setPlaying(true)
        // waveSurfer.current.playPause();
      }
    })
  }, [])

  const play = useCallback(() => {
    playingPrev.current = !playingPrev.current
    setPlaying(playingPrev.current)
    if (ready) {
      waveSurfer.current.playPause();
      waveSurfer.current.drawBuffer();
    }
  }, [ready])

  const onNext = useCallback(() => {
    // naturalnessFiles.current[sampleNext.current - 1] = [samples[sampleNext.current - 1], rating, Math.round(progress.current)]
    naturalnessFiles.current[sampleNext.current - 1] = samples[sampleNext.current - 1]
    naturalnessPercent.current[sampleNext.current - 1] = Math.round(progress.current)
    naturalnessValue.current[sampleNext.current - 1] = rating
    // console.log(sampleNext.current, naturalnessFiles.current, naturalnessValue.current)
    sampleNext.current++
    setSampleN(sampleNext.current)
    progress.current = 0
    setPlaying(false)
    playingPrev.current = false
    enabledPrev.current = false
    setEnabled(false)
    setRating(4)
    if (sampleNext.current === samples.length + 1) {
      waveSurfer.current.playPause();
      onUpdate({ naturalnessFiles: naturalnessFiles.current, naturalnessPercent: naturalnessPercent.current, naturalnessValue: naturalnessValue.current })
    } else {
      setReady(false)
      samplePrev.current = `/survey_data${samples[sampleNext.current - 1]}`
      waveSurfer.current.load(`/survey_data${samples[sampleNext.current - 1]}`)
    }
  }, [onUpdate, samples, rating])

  return (
    <Stack direction="column" sx={{ mt: 8 }}>
      <Card sx={{ p: 3, background: 'linear-gradient(to right, rgba(2, 23, 92, 0.9), rgba(5, 11, 36, 0.9))', borderRadius: 8 }}>
        <Typography variant="h5" sx={{ mb: 1, color: 'white', textAlign: 'center' }}>
          Naturalness test {sampleN} of {samples.length}
        </Typography>

        <Stack justifyContent="center" direction="row">
          <IconButton aria-label="play/pause" color="purple" sx={{ width: '120px', height: '120px', ml: '-20px' }} onClick={play}>
            { playing ? <PauseCircleFilled style={{ width: '70px', height: '70px' }} /> : <PlayCircleFilled style={{ width: '70px', height: '70px' }} />}
          </IconButton>

          <Box sx={{ width: '500px', height: '120px', ml: '-10px', mt: '1px' }}>
            <WaveSurfer onMount={handleWSMount}>
              <WaveForm
                id="waveform"
                interact="true"
                fillParent="false"
                barWidth="3"
                cursorWidth="1"
                backend="WebAudio"
                width="450"
                height="120"
                progressColor="#2D5BFF"
                removeMediaElementOnDestroy="false"
                responsive="true"
                waveColor="#EFEFEF"
                cursorColor="transparent"
                minPxPerSec="10"
              />
            </WaveSurfer>
          </Box>
        </Stack>
        <Box sx={{ mx: 5, mt: 1 }}>
          <Slider
            color="secondary"
            aria-label="naturalness"
            defaultValue={4}
            step={0.5}
            min={1}
            max={5}
            valueLabelDisplay="auto"
            marks={width > 550 ? marks : marksShort}
            value={rating}
            onChange={(event, value) => setRating(value)}
          />
        </Box>
      </Card>
      <Button variant="contained" size="large" color="purple" endIcon={<Send/>} onClick={onNext} disabled={!enabled}
              sx={{mt: 4, width: 300, alignSelf: 'center'}}>
        {sampleN === samples.length ? 'Submit and finish' : 'Submit and go to next'}
      </Button>
    </Stack>
  );
}

AudioNatural.propTypes = {
  onUpdate: PropTypes.func,
};
