import {setSurveyData} from "../../firestore"
import { getStorage, ref, uploadString, getDownloadURL } from "firebase/storage";
const { TelegramClient } = require('messaging-api-telegram')

export function accountSet (payload) {
  return { type: 'account/set', payload };
}

// async thunk actions

export function saveSurvey () {
  return async (dispatch, getState) => {
    // console.log(dispatch, getState().account)
    try {
      setSurveyData(getState().account)
    } catch (error) {
      console.log(error)
      return Promise.reject(new Error('Server error'));
    }
  };
}

export function sendTelegram (data) {
  return async (dispatch, getState) => {
    try {
      const account = getState().account
      const client = new TelegramClient({
        accessToken: '2144200952:AAHhwi7j9HtCiiIzGuFwYLJuZ4khST5vIl8',
      });

      let naturalnessControl = ''
      for (let i=0; i<account.naturalnessFiles?.length; i++) {
        if (account.naturalnessFiles[i].includes('control')) {
          naturalnessControl += account.naturalnessValue[i] + ' '
        }
      }

      let similarityControl = ''
      for (let i=0; i<account.similarityFiles?.length; i++) {
        if (account.similarityFiles[i].includes('control')) {
          similarityControl += account.similarityValue[i] + ' '
        }
      }

      const message = `New survey!\nTurkId: ${account.turkId}\nNaturalness control: ${naturalnessControl}\nSimilarity control: ${similarityControl}\nCompletion code: ${account.completionCode}`

      client.sendMessage(348558282, message)
      client.sendMessage(469187097, message)

      if (account.naturalnessFiles) {

        let xls = 'File name\trating\tpercent of listening\t\n';

        for (let i = 0; i < account.naturalnessFiles.length; i++) {
          xls += account.naturalnessFiles[i] + '\t' + account.naturalnessValue[i] + '\t' + account.naturalnessPercent[i] + '\n'
        }

        // console.log(xls)

        const storage = getStorage();
        const storageRef = ref(storage, 'survey_' + new Date().toLocaleDateString('ru') + '_' + account.turkId + '_naturalness.xls');

        uploadString(storageRef, xls).then((snapshot) => {
          getDownloadURL(snapshot.ref).then((downloadURL) => {
            console.log('File available at', downloadURL);
            client.sendMessage(348558282, downloadURL)
            client.sendMessage(469187097, downloadURL)
          });
        });
      }

      if (account.similarityFiles) {

        let xls = 'File name\tFile name2\trating\tpercent of listening\tpercent of listening 2\t\n';

        for (let i = 0; i < account.similarityFiles.length; i++) {
          xls += account.similarityFiles[i] + '\t' + account.similarityFiles2[i] + '\t' + account.similarityValue[i] + '\t' + account.similarityPercent[i] + '\t' + account.similarityPercent2[i] + '\n'
        }

        // console.log(xls)

        const storage = getStorage();
        const storageRef = ref(storage, 'survey_' + new Date().toLocaleDateString('ru') + '_' + account.turkId + '_similarity.xls');

        uploadString(storageRef, xls).then((snapshot) => {
          getDownloadURL(snapshot.ref).then((downloadURL) => {
            console.log('File available at', downloadURL);
            client.sendMessage(348558282, downloadURL)
            client.sendMessage(469187097, downloadURL)
          });
        });
      }

      //

    } catch (error) {
      console.log(error)
      return Promise.reject(new Error('Server error'));
    }
  };
}

