import PropTypes from 'prop-types';
import {useCallback, useState} from "react";
import {Button, Stack, TextField} from "@mui/material";
import {Send} from '@material-ui/icons';
import {useSelector} from "react-redux";
import {accountTurkId} from "../store/selectors/account.selector";

export default function InputId(props) {
  const { onUpdate, onTap } = props;
  const [turkId, setTurkId] = useState(useSelector(accountTurkId))

  const onMouseDown = useCallback((value) => {
    onTap(true)
  }, [onTap])

  const onMouseUp = useCallback((value) => {
    onTap(false)
  }, [onTap])

  return (
    <Stack direction="column" sx={{ mt: 8 }}>
      <TextField autoFocus label="Enter your Mechanic Turk ID" variant="filled" value={turkId}
                 onChange={(event) => setTurkId(event.target.value)}
                 InputProps={{style: {background: 'linear-gradient(to left, rgba(2, 23, 92, 0.9), rgba(5, 11, 36, 0.9))'}}}
                 inputProps={{style: {fontSize: 28, color: 'white'}}}/>
      <Button variant="contained" size="large" color="purple" endIcon={<Send/>} onClick={() => onUpdate({turkId})}
              onMouseDown={onMouseDown} onMouseUp={onMouseUp} onMouseLeave={onMouseUp} onTouchStart={onMouseDown} onTouchEnd={onMouseUp} sx={{mt: 4, width: 200, alignSelf: 'center'}}>
        Next
      </Button>
    </Stack>
  );
}

InputId.propTypes = {
  onUpdate: PropTypes.func,
  onTap: PropTypes.func,
};
