import { createSelector } from 'reselect';

const selectAccount = (state) => state.account

export const accountBack = createSelector(
  selectAccount,
  (account) => {
    return account.back
  }
)

export const accountStep = createSelector(
  selectAccount,
  (account) => {
    // console.log(account)
    return account.step
  }
)

export const accountTurkId = createSelector(
  selectAccount,
  (account) => {
    return account.turkId
  }
)

export const accountDevice = createSelector(
  selectAccount,
  (account) => {
    return account.device
  }
)

export const accountCompletionCode = createSelector(
  selectAccount,
  (account) => {
    return account.completionCode
  }
)
