import { initializeApp } from "firebase/app";
import { getAnalytics, logEvent } from "firebase/analytics";
import {doc, getFirestore} from "firebase/firestore";
import { setDoc } from "firebase/firestore";

const firebaseConfig = {
  apiKey: "AIzaSyAvzZFVLs9qQGz3dRktc8cyJIqSAYbXFDs",
  authDomain: "ai-mos.firebaseapp.com",
  projectId: "ai-mos",
  storageBucket: "ai-mos.appspot.com",
  messagingSenderId: "1078422043507",
  appId: "1:1078422043507:web:95c0e60ae10aa41ffe6306",
  measurementId: "G-H4TLS5MPHC"
};

const app = initializeApp(firebaseConfig);

export const db = getFirestore(app)

export const setSurveyData = async (data) => {
  // console.log(222, data)
  return await setDoc(doc(db, 'survey', data.turkId), data);
}

export const getVenueCollection = (collectionName) => {
  return db.collection("venueCollections").doc(collectionName).get();
}

export const getFeaturedVideos = () => {
  return db
    .collection("venues")
    .where("featured_video", "==", true)
    .where("video_thumb", ">", "")
    .get();
}

export const observeLiveStreamDoc = (liveId, observer) => {
  return db
    .collection('live')
    .doc(liveId)
    .onSnapshot(observer)
}

export const analytics = getAnalytics(app);

export const analyticsEvent = (name, data) => {
  return logEvent(analytics, name, data)
}

export const analyticsPageEvent = (page) => {
  analytics.setCurrentScreen(page);
  return analytics.logEvent('page_view', {
    page_path: page,
  });
}
